.pleer_layer {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    .pleer_box {
        position: relative;
        width: calc(100% - 60px);
        max-width: 1200px;
        height: 90%;
        max-height: 900px;

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;

        .wheel-rect{
            width: 100%;
            height: calc(100% - 35px);

            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: center;

            h1 {
                // border: 1px solid red;
                color: white;
                font-family: 'Inter';
                font-style: normal;
                font-weight: 400;
                font-size: 40px;
                line-height: 40px;
                max-width: 60%;
                height: 100px;
                text-align: center;
                text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
            }
            .place{
                // border: 1px solid red;
                flex-grow: 1;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
    
                .play-pause{
                    cursor: pointer;
                    position: absolute;
                    left: calc(50% - 50px);
                    top: calc(50% - 50px);
                    width: 100px;
                    height: 100px;
                    background-size: contain;
                    background-position: center;
                    background-repeat: no-repeat;
                    transition: transform 0.1s ease 0s;
                    transform: scale(1);
                    &.play{
                        background-image: url('./../../../assets/pause.svg');
                    }
                    &.pause{
                        background-image: url('./../../../assets/play.svg');
                    }
                    &.hide{
                        transform: scale(0);
                    }
                }
            }
            .prevnext{
                position: absolute;
                width: 50px;
                height: 50px;
                top: calc(50% - 25px);
                cursor: pointer;
                
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                opacity: 1;
                transition: 0.3s;
                &.opacity{
                    opacity: 0.5;
                }
            }
            .prevbook{
                left: 25%;
                background-image: url('./../../../assets/prevbook.svg');
            }
            .nextbook{
                right: 25%;
                background-image: url('./../../../assets/nextbook.svg');
            }
        }
    }
    @media (max-width: 1200px) {
        .pleer_box {
            .wheel-rect{
                h1{
                    font-size: 40px;
                    line-height: 40px;
                }
                .prevbook{
                    left: 0;
                }
                .nextbook{
                    right: 0;
                }
            }
        }
    }
}